import React from 'react'
import Accordion from 'react-bootstrap/Accordion';
import './styles/acordion.css'

const Acordion = () => {
  return (
    <Accordion className='acordion'>
        <Accordion.Item eventKey="0" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de juguetes</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Jornada "Da de la niez", campaa solidaria con el objetivo de celebrar con los nios ms carenciados de nuestra localidad. Se colabor con juguetes, dulces, alimentos para la merienda, ropa de nios y bebs.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de alimentos #1</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Merendero "Estrellita Virgen del Lujn", entregamos una bolsa de zanahoria, una bolsa de harina y un bidn de aceite.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de vestimenta #1</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Campaa solidaria "Boca te abriga", colecta de ropa de abrigo, bufandas, calzados, frazada, con el fin de que las personas mas necesitadas puedan combatir el fro. Todas las donaciones fueron destinadas al merendero "Estrellita Virgen del Lujn", donde adems se colabor con alimentos para el almuerzo.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donaciones varias #1</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Jornada solidaria donde se hizo entrega de artculos, tiles escolares y diferentes libros a la escuela 6194 Almafuerte del Paraje el Omb, zona rural de Arroyo Seco.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="4" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de medicamentos #1</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Entrega de medicamentos al centro de salud "Eva Pern" (SAMCO) de la localidad de Fighiera, y al centro de salud del barrio Costa del Paran.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="5" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de alimentos #2</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Entrega de cajones de pollo con el fin de ayudar a Fabin Damelio quien padece de una enfermedad neurodegenerativa (ELA).
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="6" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donaciones varias #2</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Campaa de donacin de tapitas plsticas. La pea es sede oficial del hospital Garrahan para poder depositar tapitas para su utilizacin. Estas se derriten y se convierten en kit de elementos de limpieza y dems elementos necesarios.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="7" className='acordion-item'>
            <Accordion.Header className='acordion-title'>Donacin de medicamentos #2</Accordion.Header>
            <Accordion.Body className='acordion-info'>
                Se entregaron medicamentos al centro de salud "Ramn Carrillo" de Arroyo Seco, donde algunos se llevaron al hospital SAMCO N 50.
            </Accordion.Body>
        </Accordion.Item>
    </Accordion>
  )
}

export default Acordion
